import React, { Suspense, useContext } from 'react';
import { Redirect, Route, Switch } from 'react-router';

import { AuthContext } from './modules/auth/authContext';
import AuthRoutes from './modules/auth/routes';
import HomeRoutes from './modules/home/routes';
import CameraRoutes from './modules/cameras/routes';
import ExploreRoutes from './modules/explore/routes';
import AnalyticsRoutes from './modules/analytics/routes';
import UsersRoutes from './modules/users/routes';

import NotFound from './shared/templates/notFound';

const PrivateRoute = ({ component: Component, ...rest }) => {
  const authContext = useContext(AuthContext);
  return (
    <Route
      {...rest}
      exact
      render={props => (authContext.checkAuthentication() ? <Component {...props} /> : <Redirect to="/" />)}
    />
  );
};

const renderRouteFromList = isPrivate => (item, i) => {
  const { Component } = item;
  if (isPrivate) {
    return <PrivateRoute exact key={i} path={item.path} component={Component} />;
  }
  return <Route exact key={i} path={item.path} component={Component} />;
};

const Routes = () => (
  <Suspense fallback="loading">
    <Switch>
      {AuthRoutes.map(renderRouteFromList())}
      {HomeRoutes.map(renderRouteFromList(true))}
      {CameraRoutes.map(renderRouteFromList(true))}
      {ExploreRoutes.map(renderRouteFromList(true))}
      {AnalyticsRoutes.map(renderRouteFromList(true))}
      {UsersRoutes.map(renderRouteFromList(true))}
      <Route component={NotFound} />
    </Switch>
  </Suspense>
);

export default Routes;
